html{
  scroll-behavior: smooth;
}

.wrap__campaign {
  .preview__campaign {
    .card {
      background-color: #102279;
      border-radius: 0;
      .popular__title {
        color: white;
      }
      .row {
        display: flex;
        .column {
          flex: 50%;
          display: flex;
          justify-content: space-around;
        }
        @media screen and (max-width: 900px) {
          .column {
            flex: 50%;
            display: grid;
            justify-content: center;
          }
        }
      }
    }
  }

  .description__campaign {
    height: auto;
    background-color: #ffb73e;
    padding: 10px;
    text-align: center;
    p {
      margin-block-start: 0;
      margin-block-end: 0;
      color: black;
    }
  }

  .contact__campaign {
    .col-10.col-m-12 {
      padding: 0;
    }
    .card {
      border-radius: 0;
    }
  }

  .features__campaign {
    padding: 0 10px;
    .section {
      .about-app {
        padding-top: 123px;
        .about-app__description {
          display: flex;
          justify-content: center;
          .about-app__description-content--left {
            margin-left: 0;
            text-align: center;
          }
        }
      }
      @media screen and (max-width: 1200px) {
        .about-app {
          margin-top: -80px;
        }
      }
    }
  }

}
